import { Component, Host, h, Prop } from '@stencil/core';

type MtCssClassMap = { [className: string]: boolean };

const generateClasses = (
  produto: string | undefined | null, // MtProdutos | undefined | null,
  especialidade: string | undefined | null, // MtMedcursoEspecialidade | undefined | null,
  ano: string | undefined | null,
  component: string | undefined | null,
  cssClassMap: MtCssClassMap,
): MtCssClassMap => {
  return typeof produto === 'string' && produto.length > 0
    ? {
        [`mt-${produto}-${component}`]: true,
        [`mt-${especialidade}-${component}`]: true,
        [`mt-${ano}-${component}`]: true,
        ...cssClassMap,
      }
    : cssClassMap;
};

@Component({
  tag: 'mt-title-h1',
  styleUrl: 'title-h1.scss',
  scoped: true,
})
export class TitleH1 {
  @Prop({ reflect: true }) produto: 'medcurso' | 'med'; // MtProdutos

  @Prop({ reflect: true }) especialidade: 'car' | 'nef'; // MtMedcursoEspecialidade

  @Prop({ reflect: true }) ano: '202';

  render() {
    const { produto, especialidade, ano } = this;

    return (
      <Host class={generateClasses(produto, especialidade, ano, 'title-h1', {
          'mt-title-h1': true,
        })}>

          <p class="mt-title-h1__pre-title">
            <slot name="pre-title"></slot>
          </p>

          <h1 class="mt-title-h1__title">
            <slot name="title"></slot>
          </h1>

          <h2 class="mt-title-h1__subtitle">
            <slot name="subtitle"></slot>
          </h2>

      </Host>
    );
  }
}
